

body {
    width: 100vw;
    height: 100vh;
    overflow: auto;
    overflow-x: hidden;
    background-color: rgb(238, 238, 238);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

img {
    border-radius: 10px;
}

#app {
    width: 100%;
    height: 100%;
}

.items-center {
    text-align: center;
    align-items: center;
    display: flex;
    justify-content: center;
}

.w-90 {
    width: 90%;
}

.w-49 {
    width: 49%;
}

.w-99 {
    width: 99%;
}

.btn {
    margin: 2px;
}

.m-0 {
    margin-top: 0px;
}

.col-center {
    flex-direction: column;
    align-items: center;
}

.screen {
    width: 80%;
    margin: auto;
    height: fit-content;
    min-height: 60%;
    background-color: rgb(212, 212, 212);
    border-radius: 15px;
}

.min-h {
    min-height: min-content;
    height: min-content;
}

.algin-center {
    align-items: center;
}

.screen-first {
    vertical-align: middle;
    height: 80vh;
}

.pointer {
    cursor: pointer;
}

.mt-10 {
    margin-top: 60px;
    margin-inline: auto;
}

.company-name {
    color: blue;
}

.d-flex {
    display: flex;
}

.mr-2 {
    margin-right: 12px;
}

.ml-2 {
    margin-left: 12px;
}

.j-center {
    justify-content: center;
    display: flex;
}

.in-w-75 * {
    width: 75%;
}

.in-m-auto * {
    margin: auto;
}

.font-lg {
    font-size: larger;
}

.m-w-83px {
    min-width: 83px;
}

.min-w-7 {
    min-width: 7%;
}

.overflow-y-scroll {
    overflow-y: scroll;
    height: fit-content;
}

.captcha-container {
    display: flex;
    flex-direction: column;
}

.flex {
    display: flex;
}

.in-flex-row * {
    display: flex;
    flex-direction: row;
}

.in-center * {
    text-align: center;
}

.w-60 {
    width: 60%;
}
.ml-check{
    margin-left: 5px;
}
.w-40 {
    width: 40%;
}

.w-2input{
    width: 30%;
    min-width: 50px;
}
.w-70{
    width: 70%;
}
.in-ml-3 *{
    margin-left: 36px;
}

.pdf_btn{
    position: fixed;
    top:100%;
    left: 50%;
    transform: translate(-50%,-120%);
    width: 50%;
    min-width: 400px;
}
.j-space-evenly{
    justify-content: space-evenly;
}
@media only screen and (max-width: 700px) {
    .screen {
        width: 100%;
        border-radius: 0px;
    }
}

@media only screen and (min-width: 1200px) {
    .screen {
        width: 80%;
    }
}
.active{
    background-color: #a1a1ff;
    border-radius: 10px;
}